@import url("https://fonts.googleapis.com/css2?family=Karla:wght@400;700&display=swap");
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

ul {
  list-style: none;
}

body {
  font-family: "Karla", sans-serif;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
  background-color: #e5eff5;
}
body main {
  max-width: 80vw;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
body main .card {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, 0.8fr);
  column-gap: 0;
  row-gap: 0;
  border-radius: 0.625rem;
  overflow: hidden;
  max-width: 40rem;
}
@media (min-width: 40em) {
  body main .card {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
}
body main .card__top-row {
  grid-area: 1/1/2/2;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (min-width: 40em) {
  body main .card__top-row {
    grid-area: 1/1/2/3;
    justify-content: flex-start;
  }
}
body main .card__top-row .top-row__main-text {
  margin: 2.1875rem 1.875rem 1.875rem 1.3125rem;
}
body main .card__top-row .top-row__main-text h1 {
  font-weight: 700;
  font-size: 1.3125rem;
  letter-spacing: -4%;
  color: #2ab2af;
  margin-bottom: 1.875rem;
}
@media (min-width: 40em) {
  body main .card__top-row .top-row__main-text h1 {
    font-size: 1.4125rem;
  }
}
body main .card__top-row .top-row__main-text h2 {
  font-weight: 700;
  font-size: 1rem;
  line-height: 1.16875rem;
  letter-spacing: -4%;
  color: #c0df34;
  margin-bottom: 0.9625rem;
}
@media (min-width: 40em) {
  body main .card__top-row .top-row__main-text h2 {
    font-size: 1.05rem;
  }
}
body main .card__top-row .top-row__main-text p {
  font-weight: 400;
  font-size: 15px;
  line-height: 1.5625rem;
  letter-spacing: -4%;
  color: #a5abbb;
}
body main .card__top-row .top-row__main-text h1,
body main .card__top-row .top-row__main-text h2,
body main .card__top-row .top-row__main-text p {
  max-width: 15.625rem;
}
@media (min-width: 40em) {
  body main .card__top-row .top-row__main-text h1,
body main .card__top-row .top-row__main-text h2,
body main .card__top-row .top-row__main-text p {
    max-width: 85%;
  }
}
body main .card__bottom-left {
  grid-area: 2/1/3/2;
  background-color: #2ab2af;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 100%;
  margin-bottom: 0;
}
@media (min-width: 40em) {
  body main .card__bottom-left {
    grid-area: 2/1/3/2;
  }
}
body main .card__bottom-left .bottom-left__main-text {
  margin: 1.5625rem 3.125rem 1.5625rem 1.5625rem;
}
@media (min-width: 40em) {
  body main .card__bottom-left .bottom-left__main-text {
    max-width: 80%;
  }
}
body main .card__bottom-left .bottom-left__main-text h2 {
  font-weight: 700;
  font-size: 1.3125rem;
  letter-spacing: -4%;
  color: white;
  opacity: 85%;
  margin-bottom: 1.25rem;
}
body main .card__bottom-left .bottom-left__main-text .main-text__money-info {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 0.625rem;
}
body main .card__bottom-left .bottom-left__main-text .main-text__money-info .money {
  font-weight: 700;
  font-size: 2.125rem;
  letter-spacing: -5.5%;
  color: white;
  opacity: 90%;
}
body main .card__bottom-left .bottom-left__main-text .main-text__money-info .label {
  font-weight: 400;
  font-size: 1rem;
  letter-spacing: -3%;
  color: #6ad4d7;
  margin-top: 0.3rem;
  margin-left: 0.7375rem;
}
body main .card__bottom-left .bottom-left__main-text p {
  font-weight: 400;
  font-size: 1.0625rem;
  letter-spacing: -4%;
  color: white;
  opacity: 77%;
}
body main .card__bottom-left button {
  height: 3.125rem;
  width: 80%;
  border-radius: 0.35rem;
  border: none;
  background-color: #c0df34;
  box-shadow: 0px 6px 12px #207D7C;
  cursor: pointer;
  font-family: "Karla", sans-serif;
  font-weight: 700;
  font-size: 1.0625rem;
  letter-spacing: -4%;
  color: rgba(255, 255, 255, 0.76);
  margin-bottom: 1.5625rem;
}
@media (min-width: 40em) {
  body main .card__bottom-left button {
    width: 75%;
    align-self: flex-start;
    margin-left: 1.5625rem;
  }
}
body main .card__bottom-right {
  grid-area: 3/1/4/2;
  background-color: #4ABEBD;
  margin-top: 0;
}
@media (min-width: 40em) {
  body main .card__bottom-right {
    grid-area: 2/2/3/3;
  }
}
body main .card__bottom-right .bottom-right__main-text {
  margin-bottom: 1.5625rem;
  margin-left: 1.25rem;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
body main .card__bottom-right .bottom-right__main-text h2 {
  font-weight: 700;
  font-size: 1.5125rem;
  letter-spacing: -4%;
  color: white;
  opacity: 85%;
  margin-bottom: 1.25rem;
}
body main .card__bottom-right .bottom-right__main-text .main-text__feature-list li {
  font-weight: 400;
  font-size: 0.875rem;
  letter-spacing: 1%;
  color: #B6F9FF;
  opacity: 83%;
  margin-bottom: 0.325rem;
}